.Ballot {    
    float: left;
    border-style: solid;
    display: flex;
    flex-direction: column;
    align-items: center;
}

ul {
    list-style-type: none;
    display: inline-block;
    padding: 0;
    font-size: x-small;
    width: 10em;
    height: 10em;
}

li {
    display: inline-block;
    width: 100%;
    border-bottom: 1px solid;
}

button {
    background-color: darkslategray;
    border: 1px solid;
    color: white;
    padding: 12px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}

button:disabled {
    opacity: 0.6;
}

.elected {
    color: green;
    background-color: lightgreen;
}
  
.eliminated {
    color: #666666;
    text-decoration: line-through;
    background-color: lightgray;
}

.animated {
    transition: all 0.5s ease;
}

.candidateTable{
    margin: auto;
    display:inline-block;
}
  
.candidateTableWrapper{
    position:relative;
}
  
.expandButton{
    display:inline-block;
  
    float: none;
    margin-right: 0;
    position: absolute;
    top: 0;
    height: 100%;
}

table {
    border-collapse: collapse;
}

tr {
    border: 1px solid black;
    padding: 15px;
}

th {
    padding: 15px;
}

.electionSelector {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 2%;
    left: 2%;
    z-index: 2;
}